.section {
  padding: 30px 0 50px;

  &__wrap {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    @include m(1024) {
      display: flex;
      flex-direction: column; } }

  &__left {
    width: calc(100% - 300px);
    margin-right: 28px;

    @include m(1024) {
      width: 100%;
      margin-right: 0; } }

  &__right {
    width: 300px;

    @include m(1024) {
      width: 100%; } }

  &__title {
    font-size: 26px; }

  &.is-main {
    padding: 50px 0 50px; }


