.rateit {
  display: -moz-inline-box;
  display: inline-block;
  position: relative;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  .rateit-range {
    position: relative;
    display: -moz-inline-box;
    display: inline-block;
    background: url('~media/images/star.gif');
    height: 16px;
    outline: none;
    cursor: pointer;
    * {
      display: block; } } }

/* for IE 6 */

* {
  html .rateit, + html .rateit {
    display: inline;
    .rateit-range {
      display: inline; } } }

/* for IE 7 */

.rateit {
  .rateit-hover, .rateit-selected {
    position: absolute;
    left: 0;
    top: 0;
    width: 0; }
  .rateit-hover-rtl, .rateit-selected-rtl {
    left: auto;
    right: 0; }
  .rateit-hover {
    background: url('~media/images/star.gif') left -32px;
    color: rgb(239, 197, 41); }
  .rateit-hover-rtl {
    background-position: right -32px; }
  .rateit-selected {
    background: url('~media/images/star.gif') left -16px;
    color: rgb(191, 66, 66); }
  .rateit-selected-rtl {
    background-position: right -16px; }
  .rateit-preset {
    background: url('~media/images/star.gif') left -48px;
    color: rgb(239, 107, 34); }
  .rateit-preset-rtl {
    background: url('~media/images/star.gif') left -48px; }
  button.rateit-reset {
    background: url('~media/images/delete.gif') 0 0;
    width: 16px;
    height: 16px;
    display: -moz-inline-box;
    display: inline-block;
    float: left;
    outline: none;
    border: none;
    padding: 0; }
  .rateit-reset span {
    display: none; }
  button.rateit-reset {
    &:hover, &:focus {
      background-position: 0 -16px; } } }

.rateit-font {
  font-size: 24px;
  line-height: 1em;
  .rateit-range {
    background: none;
    height: auto; }
  .rateit-empty {
    color: #ccc; }
  .rateit-range > div {
    background: none;
    overflow: hidden;
    cursor: default; } }

.rateit.rateit-font {
  .rateit-reset {
    font-size: inherit;
    background: none;
    width: 0.6em;
    height: 0.6em;
    margin-top: 0.2em;
    background: gray;
    border-radius: 50%;
    position: relative;
    span {
      display: block;
      font-weight: bold;
      color: white;
      height: calc(50% - 0.045em);
      top: 0;
      position: absolute;
      border-bottom: 0.09em solid white;
      width: 72%;
      margin-left: 14%; }
    &:hover {
      background: #e6574b;
      /* Old browsers */
      background: radial-gradient(ellipse at center, #e6574b 55%, #f6836b 77%, #f9d3cc 100%);
      /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
      background-position: 0 0; } }
  button.rateit-reset:focus {
    background: #e6574b;
    /* Old browsers */
    background: radial-gradient(ellipse at center, #e6574b 55%, #f6836b 77%, #f9d3cc 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    background-position: 0 0; } }
