@include font('apksitory-icons', 'apksitory-icons');

html,
body {
  width: 100%;
  height: 100%; }

body {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  line-height: 1.4;
  color: $text;
  font-family: $font-main;
  font-weight: 400; }

a {
  display: inline-block;
  text-decoration: none;
  color: $link;
  transition: all .3s ease-in-out; }

img {
  display: inline-block;
  max-width: 100%; }

input:focus,
select:focus,
button:focus {
  outline: none; }

input[type='text'],
input[type='search'],
input[type='button'] {
  -moz-appearance: none;
  -webkit-appearance: none; }

.copyright,
.copy {
  font-size: 13px;
  font-weight: 300;
  color: $text-grey; }

.hero {
  padding-top: 65px;
  padding-bottom: 20px;
  text-align: center;
  background: url('../media/images/hero-bg.png');
  background-size: cover;

  &__wrap {
    display: inline-block;
    text-align: center;
    width: 700px; }

  &__title {
    font-size: 46px;
    font-weight: 400; }

  &__descr {
    margin-top: 15px;
    margin-bottom: 30px;
    font-size: 16px; } }

.nav-footer {
  display: flex;
  color: $text-grey;

  &__el {
    padding-right: 27px;
    position: relative;
    &:after {
      position: absolute;
      content: '';
      top: 4px;
      right: 13px;
      width: 1px;
      height: 13px;
      background-color: #90939f; }

    &:last-child {
      &:after {
        display: none; } } }

  &__link {
    color: $text-grey;
    font-size: 13px;
    font-weight: 300; } }

.information {

  ul {
    margin-bottom: 15px;

    li {
      position: relative;
      padding-left: 12px;
      padding-bottom: 2px;
      font-size: 13px;
      font-weight: 300;

      &:after {
        position: absolute;
        top: 8px;
        left: 0;
        content: "";
        width: 3px;
        height: 3px;
        background-color: $text; } } }

  p {
    margin-bottom: 25px;
    font-size: 13px;
    font-weight: 300; }

  span {
    font-size: 18px;
    font-weight: 600;
    line-height: 1.8; }

  h1 {
    font-size: 28px;
    font-weight: 300;
    margin-bottom: 20px; } }

.pre-footer {

  &__wrap {
    padding: 30px 0;
    display: flex;
    border-top: 1px solid $color-border; } }

.popular {
  display: flex;

  &__list {
    margin-left: 4px;
    display: flex; }

  &__el {
    padding-right: 15px;
    position: relative;
    &:after {
      position: absolute;
      content: '';
      top: 4px;
      right: 7px;
      width: 1px;
      height: 13px;
      background-color: #616161; }

    &:last-child {
      &:after {
        display: none; } } }

  &__link {
    color: $text;
    font-size: 13px;
    &:hover {
      color: $link; } } }

.apps {
  margin-bottom: 24px;
  display: flex;
  flex-wrap: wrap;

  .app {
    width: 33.33%; } }

#describe {
  margin-top: 20px;
  line-height: 20px;
  height: 200px;
  overflow: hidden;
  position: relative; }

.showmore-trigger {
  position: relative;
  margin-bottom: 20px;
  span {
    cursor: pointer;
    line-height: 30px;
    padding-right: 15px;
    height: 30px;
    display: inline-block;
    color: #00A6ED;
    text-decoration: none;
    background: url(https://static.apkpure.com/www/static/imgs/read-more.png) no-repeat right 13px; } }

.show-more-end {
  background: linear-gradient(to bottom, rgba(255,255,255,0), #fff);
  filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0,StartColorStr='#00ffffff',EndColorStr='#ffffff');
  top: -30px;
  height: 30px;
  left: 0;
  position: absolute;
  width: 100%; }

.description {
  //overflow: hidden
  //height: 200px
  //margin-top: 20px
  //margin-bottom: 20px
  //padding-bottom: 20px
  //border-bottom: 1px solid $color-border

  h2 {
    margin-bottom: 10px;
    font-size: 20px;
    font-weight: 500; }
  p {
    margin-bottom: 15px; } }

.changelog {
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid $color-border;

  h2 {
    margin-bottom: 10px;
    font-size: 20px;
    font-weight: 500; } }

.tags {
  &__link {
    a {
      margin-right: 10px;
      padding: 0 15px;
      height: 30px;
      line-height: 30px;
      color: $link;
      border: 1px solid $link; } } }

.stars {
  margin-bottom: 5px;
  background: url('../media/images/star-empty.png') repeat-x;
  height: 12px;
  width: 60px;
  clear: both;
  direction: ltr;
  position: relative; }

.stars span.score {
  display: block;
  background: url('../media/images/star.png') repeat-x;
  height: 12px;
  width: 60px; }

.stars span.star {
  position: absolute;
  left: 65px;
  line-height: 12px;
  top: 1px;
  color: #f4cd38;
  font-size: 13px; }



.games {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  .game {
    width: 16.6%; } }

.game {
  width: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
  border: 1px solid transparent;
  background-color: #ffffff;
  transition: all .3s ease-in-out;
  box-sizing: border-box;
  &:hover {
    background-color: #fafafa;
    border-color: $color-border; }

  &__link {
    padding-top: 20px;
    padding-bottom: 7px;
    display: block;
    width: auto;
    text-align: center; }

  &__img {
    width: 100px; }

  &__name {
    display: block;
    font-size: 15px;
    font-weight: 500;
    color: $text;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    &:hover {
      color: $link; } }

  &__bottom {
    padding: 0 25px 10px 25px; }

  .stars {
    margin-top: 4px;
    margin-bottom: 8px;
    left: 15px; } }

.table {
  width: 100%;

  thead {
    tr {
      height: 46px;
      line-height: 46px; } }
  tr {
    height: 30px;
    line-height: 30px;
    border: 1px solid $color-border; }

  .rating {
    display: none;

    @include m(1024) {
      display: table-row; }

    .vote {
      margin-left: 3px;
      position: relative;
      top: -2px; } }

  td {
    padding: 0 15px;
    width: 70%;
    font-size: 13px;

    &:first-child {
      width: 30%;
      padding: 0 10px;
      font-weight: 500;
      background-color: #fafafa;
      text-align: center; }
    a {
      color: $text;
      .icon {
        margin-right: 3px; } } } }

.rate {
  text-align: center;
  height: 270px;
  box-sizing: border-box;
  border: 1px solid $color-border;

  &__title {
    display: block;
    height: 46px;
    line-height: 46px;
    font-size: 16px;
    font-weight: 500;
    background-color: #fafafa; }

  &__content {
    height: calc(100% - 46px);
    display: flex;
    flex-direction: column;
    justify-content: center; }

  &__sum {
    font-size: 68px;

    span {
      font-size: 24px;
      color: $text-light; } } }

.download {
  margin-bottom: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 46px;
  line-height: 46px;
  color: #ffffff;
  font-size: 16px;
  text-align: center;
  background-color: $bg-green;

  &:hover {
    background-color: $bg-green-light; }

  &.googleplay {
    background-color: #000000;

    &:hover {
      background-color: #343434; } }

  .icon {
    margin-right: 10px; }

  .bold {
    margin-right: 5px; } }

.games-more {
  width: 150px;
  height: 34px;
  line-height: 34px;
  color: $link;
  text-align: center;
  border: 1px solid $color-border;
  background-color: #fff;
  box-sizing: border-box;
  &:hover {
    background-color: #fafafa; } }

.apps-big {

  &__el {
    border-bottom: 1px solid $color-border; }
  &__more {
    margin: 20px 0;
    text-align: center; } }

.app-big {
  padding: 15px 15px 15px 0;
  position: relative;
  display: flex;
  width: 100%;
  height: 150px;
  background-color: #ffffff;
  transition: all .3s ease-in-out;
  box-sizing: border-box;
  cursor: pointer;

  &:hover {
    background-color: #fafafa; }

  &__left {
    margin-right: 30px; }

  &__right {
    width: calc(100% - 150px); }

  &__name {
    display: block;
    margin-bottom: 6px;
    font-size: 20px;
    font-weight: 500;
    color: $text;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap; }

  &__dev {
    margin-bottom: 14px;
    display: inline-block;
    font-size: 13px;
    color: $text-light;
    a {
      margin-left: 3px; } }

  .stars {
    margin-bottom: 8px; } }

.btn-transparent {
  display: inline-block;
  font-size: 13px;
  width: 150px;
  height: 34px;
  line-height: 33px;
  color: $link;
  text-align: center;
  border: 1px solid $color-border;
  background-color: transparent;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  &:hover {
    background-color: #fafafa; } }

.btn-download {
  width: 130px;
  height: 26px;
  line-height: 26px;
  font-size: 13px;
  text-align: center;
  color: $bg-green;
  background-color: #ffffff;
  border: 1px solid $bg-green;
  transition: all .3s ease-in-out;
  a {
    color: $bg-green; }

  &:hover {
    background-color: $bg-green;
    a {
      color: #ffffff; } } }

.bold {
  font-weight: 500; }

.green {
  color: #66bb6a; }





