.breadcrumbs-wrap {
  padding: 12px 0; }

.breadcrumbs {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;

  &__el {
    position: relative;
    padding-right: 21px;

    &:after,
    &:before {
      position: absolute;
      content: '';
      right: 10px;
      width: 1px;
      height: 5px;
      background-color: #727272; }

    &:before {
      top: 6px;
      transform: rotate(-45deg); }

    &:after {
      bottom: 5px;
      transform: rotate(45deg); }

    &:last-child {
      padding-right: 0;
      a {
        color: $text-light;
        pointer-events: none; }

      &:after,
      &:before {
        display: none; } } }

  &__link,
  &__link:visited {
    color: $text;
    font-size: 12px;

    &:hover {
      text-decoration: underline; } }

  .icon {
    top: -1px; } }
